
#note-editor {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 300px;
  overflow: auto;
  background: #f5f5f5;
  color: #686b6e;
}
#note-editor > .note-header > .btn-group > .notes-title {
    text-align: center;
    font-weight: 100;
}
#note-editor > .note-header > .btn-group > .button {
    height: 32px;
    width: 123px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background: #fff;
    font-weight: 100;
}
#note-editor > .note-header > .btn-group .active {
    background: #D4D4D4;
}
#note-editor > .list-group {
    margin-top: 25px;
}
#note-editor > .list-group > .list-group-item {
      background: white;
}
#note-editor > .list-group > .list-group-item > li {
        line-height: 40px;
        width: 300px;
        padding: 0 20px;
        font-size: 20px;
        cursor: pointer;
}
#note-editor > .list-group > .list-group-item :hover {
        background: #F5F5F5;
}
.activeNoteColor {
  background: #C5E7FF !important;
  color: #333;
}



